@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-VariableFont_wght.ttf");
  font-style: normal;
}
.header {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  min-height: 100px;
  display: flex;
  align-items: center;
  background-color: rgb(255, 255, 255);
}
@media (max-width: 767px) {
  .header {
    min-height: 80px;
  }
}
.header__logo {
  display: flex;
  align-items: center;
  margin-right: auto;
}
.header__logo-img {
  width: 197px;
  height: 32px;
  object-fit: contain;
}
@media (max-width: 767px) {
  .header__logo-img {
    width: 173px;
    height: 28px;
  }
}
.header__content {
  display: flex;
  align-items: center;
  gap: 24px;
  max-width: 1200px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0 24px;
}
@media (max-width: 767px) {
  .header__content {
    padding: 0 16px;
  }
}
.header__links {
  display: flex;
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (max-width: 1199px) {
  .header__links {
    display: none;
  }
}
.header__link {
  display: flex;
  border-bottom: 2px solid transparent;
  padding: 12px 24px;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 28px;
  color: inherit;
  text-decoration: none;
  transition: border-color 0.2s;
}
.header__link:hover {
  border-bottom-color: rgb(240, 84, 79);
}

.footer {
  display: flex;
  align-items: center;
  margin-top: auto;
  background-color: rgb(41, 41, 41);
}
.footer__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px;
  max-width: 1200px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 64px 24px;
}
@media (max-width: 1199px) {
  .footer__content {
    gap: 24px;
    padding: 24px;
  }
}
@media (max-width: 767px) {
  .footer__content {
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding: 16px;
  }
}
.footer__block {
  display: flex;
  flex-direction: column;
  gap: 32px;
  padding: 32px 0;
}
@media (max-width: 1199px) {
  .footer__block {
    gap: 16px;
    padding: 16px 0;
  }
}
.footer__logo {
  display: flex;
  align-items: center;
}
.footer__logo-img {
  width: 247px;
  height: 40px;
  object-fit: contain;
}
.footer__copyright {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: rgb(255, 255, 255);
}
.footer__links {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (max-width: 1199px) {
  .footer__links {
    margin-left: 50px;
  }
}
@media (max-width: 767px) {
  .footer__links {
    margin-left: 0;
  }
}
.footer__links-element {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 36px;
}
.footer__links-element::after {
  position: absolute;
  content: "";
  left: 0;
  top: 14px;
  width: 24px;
  height: 4px;
  border-radius: 10px;
  background-color: rgb(240, 84, 79);
}
.footer__link {
  color: rgba(255, 255, 255, 0.92);
  text-decoration: none;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 160px;
  padding: 10px 24px;
  margin: 0;
  border-width: 2px;
  border-style: solid;
  border-radius: 2px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.2s;
}
.button.l {
  font-size: 18px;
  line-height: 28px;
}
@media (max-width: 767px) {
  .button.l {
    min-width: auto;
    padding: 10px 28px;
    font-size: 16px;
    line-height: 24px;
  }
}
.button.primary {
  border-color: rgb(240, 84, 79);
  background-color: rgb(240, 84, 79);
  color: rgba(255, 255, 255, 0.92);
}
.button.primary:hover:not(:disabled) {
  border-color: rgb(144, 50, 47);
  background-color: rgb(144, 50, 47);
}
.button.primary:disabled {
  opacity: 0.48;
}
.button.secondary {
  background-color: rgb(255, 255, 255);
  border-color: rgb(240, 84, 79);
  color: rgb(41, 41, 41);
}
.button.secondary:hover:not(:disabled) {
  background-color: rgb(252, 221, 220);
}
.button.secondary:disabled {
  opacity: 0.48;
}

.accordion {
  display: flex;
  flex-direction: column;
  gap: 2px;
}
.accordion__item {
  padding: 32px;
  border-radius: 2px;
}
.accordion__item:nth-child(5n+1) {
  background-color: #feeeed;
}
.accordion__item:nth-child(5n+2) {
  background-color: #fcdddc;
}
.accordion__item:nth-child(5n+3) {
  background-color: #fbccca;
}
.accordion__item:nth-child(5n+4) {
  background-color: #f9bbb9;
}
.accordion__item:nth-child(5n+5) {
  background-color: #f8aaa7;
}
@media (max-width: 767px) {
  .accordion__item {
    padding: 24px;
  }
}
@media (max-width: 767px) {
  .accordion__item {
    padding: 16px;
  }
}
.accordion__header {
  display: flex;
  justify-content: space-between;
  gap: 32px;
  font-size: 24px;
  font-weight: 600;
  line-height: 32px;
  cursor: pointer;
  transition: all 0.2s;
}
@media (max-width: 767px) {
  .accordion__header {
    gap: 16px;
    font-size: 16px;
    line-height: 24px;
  }
}
.accordion__header-text {
  display: flex;
  align-items: baseline;
  gap: 20px;
}
@media (max-width: 767px) {
  .accordion__item-number {
    display: none;
  }
}
.accordion__header-icon-wrapper {
  align-self: center;
  flex-shrink: 0;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 2px;
  transition: all 0.2s;
}
@media (max-width: 767px) {
  .accordion__header-icon-wrapper {
    width: 40px;
    height: 40px;
  }
}
.accordion__icon {
  width: 32px;
  height: 32px;
  color: rgb(41, 41, 41);
  transition: all 0.2s;
}
@media (max-width: 767px) {
  .accordion__icon {
    width: 24px;
    height: 24px;
  }
}
.accordion__icon_minus {
  display: none;
}
.accordion__icon path {
  transition: fill 0.2s;
}

.accordion__item_show .accordion__icon_plus {
  display: none;
}
.accordion__item_show .accordion__icon_minus {
  display: block;
}

.accordion__header:hover .accordion__header-icon-wrapper {
  background-color: rgba(0, 0, 0, 0.04);
}
.accordion__header:hover .accordion__icon {
  color: rgb(240, 84, 79);
}

.accordion__content {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-top: 20px;
  margin-right: 56px;
}
@media (max-width: 767px) {
  .accordion__content {
    margin-right: 0;
  }
}
.accordion__content ul {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 0;
  margin: 0;
  list-style: none;
}
.accordion__content ul li {
  position: relative;
  padding-left: 36px;
}
@media (max-width: 767px) {
  .accordion__content ul li {
    padding-left: 24px;
  }
}
.accordion__content ul li::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 4px;
  border-radius: 20px;
  background-color: rgb(240, 84, 79);
}
@media (max-width: 767px) {
  .accordion__content ul li::after {
    width: 16px;
    height: 2px;
  }
}

.accordion__item:not(.accordion__item_show) .accordion__body {
  display: none;
}

.checkbox-wrapper {
  display: flex;
  flex-direction: column;
}

.checkbox {
  display: flex;
  flex-direction: column;
}
.checkbox input[type=checkbox] {
  display: none;
}
.checkbox input[type=checkbox]:checked + label {
  color: rgb(41, 41, 41);
}
.checkbox input[type=checkbox]:checked + label::before {
  border-color: rgb(21, 136, 224);
  background-color: rgb(21, 136, 224);
}
.checkbox input[type=checkbox]:checked + label .checkbox__icon-ok {
  opacity: 1;
}
.checkbox input[type=checkbox].error + label::before {
  border-color: rgb(240, 84, 79);
}
.checkbox__label {
  position: relative;
  padding-left: 32px;
  color: rgba(41, 41, 41, 0.8);
  font-size: 16px;
  line-height: 24px;
  cursor: pointer;
  user-select: none;
  transition: color 0.2s;
}
.checkbox__label:hover {
  color: rgb(41, 41, 41);
}
.checkbox__label:hover::before {
  border-color: rgb(41, 41, 41);
}
.checkbox__label::before {
  content: "";
  position: absolute;
  left: 2px;
  top: 2px;
  width: 20px;
  height: 20px;
  border-radius: 2px;
  border: 2px solid rgba(41, 41, 41, 0.56);
  background-color: rgb(255, 255, 255);
  box-sizing: border-box;
  transition: all 0.2s;
}
.checkbox__label a {
  text-decoration: none;
  color: rgb(17, 109, 179);
}
.checkbox__alert {
  display: none;
  margin-top: 4px;
  font-size: 14px;
  line-height: 20px;
  color: rgb(240, 84, 79);
}
.checkbox__icon-ok {
  position: absolute;
  left: 6px;
  top: 6px;
  width: 12px;
  height: 12px;
  color: rgb(255, 255, 255);
  opacity: 0;
  transition: opacity 0.2s;
}
.checkbox input.alert + label::before {
  border-color: rgb(240, 84, 79);
}
.checkbox input.alert + label .checkbox__alert {
  display: block;
}

.popup-auth {
  z-index: 2000;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.48);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.popup-auth__box {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  width: 560px;
  padding: 0 32px;
  background-color: rgb(255, 255, 255);
}
@media (max-width: 767px) {
  .popup-auth__box {
    width: 100%;
    height: 100%;
    padding: 0 16px;
  }
}
.popup-auth__header {
  position: relative;
  display: flex;
  padding: 16px 0;
}
.popup-auth__button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  margin-left: auto;
  border: none;
  border-radius: 2px;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}
.popup-auth__button:hover {
  background-color: rgba(0, 0, 0, 0.08);
}
.popup-auth__icon {
  width: 32px;
  height: 32px;
  color: rgb(41, 41, 41);
  transition: color 0.2s;
}
.popup-auth__content {
  overflow: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0 32px 32px;
}
@media (max-width: 767px) {
  .popup-auth__content {
    padding: 0;
  }
}
.popup-auth__content-title-wrapper {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 32px;
  padding-bottom: 16px;
}
@media (max-width: 767px) {
  .popup-auth__content-title-wrapper {
    margin-bottom: 16px;
  }
}
.popup-auth__content-title {
  font-size: 32px;
  font-weight: 600;
  line-height: 44px;
}
@media (max-width: 767px) {
  .popup-auth__content-title {
    font-size: 24px;
    line-height: 32px;
  }
}
.popup-auth__content-sub-title {
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}
.popup-auth__form {
  display: flex;
  flex-direction: column;
  margin-bottom: 32px;
}
.popup-auth__form-email {
  margin-bottom: 16px;
}
.popup-auth__form-pass {
  margin-bottom: 16px;
}
.popup-auth__button-link {
  width: fit-content;
  border: none;
  padding: 0;
  margin: 0 0 32px;
  background-color: transparent;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: rgb(17, 109, 179);
  cursor: pointer;
}
.popup-auth__checkbox {
  margin-bottom: 32px;
}
.popup-auth__captcha {
  width: 302px;
  height: 76px;
  margin-bottom: 32px;
}
.popup-auth__apply-button {
  max-width: 160px;
}
.popup-auth__login {
  display: flex;
  gap: 4px;
  padding: 16px 0 0;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}
.popup-auth__login-button {
  border: none;
  background-color: transparent;
  padding: 0;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: rgb(17, 109, 179);
  cursor: pointer;
}

.text-input {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input__label {
  margin-bottom: 4px;
  margin-left: 2px;
  font-size: 14px;
  line-height: 20px;
}
.text-input__input {
  min-height: 48px;
  padding: 10px 34px 10px 16px;
  border-radius: 2px;
  border: 2px solid rgba(0, 0, 0, 0.24);
  background-color: rgb(255, 255, 255);
  color: inherit;
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  transition: all 0.2s;
}
.text-input__input::placeholder {
  color: rgba(41, 41, 41, 0.56);
}
.text-input__input:hover {
  border-color: rgba(0, 0, 0, 0.24);
}
.text-input__input:focus {
  outline: none;
  border-bottom-color: rgb(21, 136, 224);
}
.text-input__input:disabled {
  border-color: rgba(0, 0, 0, 0.08);
  background-color: rgba(0, 0, 0, 0.08);
}
.text-input__input.error {
  border-color: rgb(240, 84, 79);
}
.text-input__input_force-alert {
  border-color: rgb(240, 84, 79);
}
.text-input__input-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input__reset {
  display: none;
}
.text-input__show {
  display: flex;
}
.text-input__reset, .text-input__show {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 8px;
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.text-input__reset:hover svg, .text-input__show:hover svg {
  color: rgb(41, 41, 41);
}
.text-input__reset.active, .text-input__show.active {
  display: flex;
}
.text-input__icon {
  color: rgb(41, 41, 41);
  transition: all 0.2s;
}
.text-input__alert {
  display: none;
  margin-top: 4px;
  color: rgb(240, 84, 79);
  font-size: 14px;
  line-height: 20px;
}
.text-input__alert.active {
  display: block;
}
.text-input__addon {
  margin-top: 4px;
  font-size: 14px;
  line-height: 20px;
}
.text-input__sub-text {
  margin-top: 4px;
  color: rgba(41, 41, 41, 0.8);
  font-size: 14px;
  line-height: 20px;
}
.text-input__force-alert {
  margin-top: 4px;
  color: rgb(240, 84, 79);
  font-size: 14px;
  line-height: 20px;
